import loginSheet from '../../components/loginSheet';
import shopItem from '../../components/shopItem';
import callUs from '../../components/callUs';
import { getLocation, getSetting } from '../../libs/auth.js';
import { scan } from '../../libs/utils';
import { get, post } from '../../libs/request.js';
export default {
	data() {
		return {
			markers: [],
			addressInfo: null,
			userInfo: null,
			shopItemData: {},
			showSheet: false,
			showShopItem: false,
			showCallUs: false,
			orders: {}, // 是否有租借中的订单
			outSn:"",
			scale:14
		};
	},












	onLoad(e) {
		let datas = decodeURIComponent((e || {}).q);
		if(datas && datas !== "undefined"){
			let code = datas.split('/');
			this.outSn = code[code.length - 1] || "";
		}
	},

	onShow(){
		this.checkAuth();
	},
	onHide(){
		this.outSn = '';
	},
	components: {
		loginSheet,
		shopItem,
		callUs
	},
	methods: {
		async checkAuth() {
			let that = this;
			this.authCode = uni.getStorageSync('authCode');
			this.addressInfo = uni.getStorageSync('user_address') || null;
			console.log("this.addressInfo",this.addressInfo)
			if (!this.authCode) {
				that.showSheet = true;
			} else {
				if (this.addressInfo) {
					that.checkAddress();
					this.getUserInfo();
					this.getUserOrder({
						leaseStatus: 2
					});
				}
			}
			
		},
		async checkAddress() {
			let that = this;
			console.log('获取定位，每次执行')
			that.addressInfo = await getLocation();
			that.getNearDevice({
				lat: that.addressInfo['latitude'],
				lng: that.addressInfo['longitude'],
				range: 2000
			});
		},
		authSuccess(e) {
			this.authCode = uni.getStorageSync('authCode') || null;
			this.showSheet = false;
			if (this.authCode) {
				this.checkAddress();
				this.getUserInfo();
				this.getUserOrder({
					leaseStatus: 2
				});
			}
		},
		getUserInfo(){
			get('/login/info').then(res => {
				if(res.code === 200){
					this.userInfo = res.data
				}
			});
		},
		makertap(e) {
			console.log(e);
			let id = e.markerId;
			post('shop/detail', {
				sid: id,
				lat: this.addressInfo['latitude'],
				lng: this.addressInfo['longitude'],
			}).then(res => {
				if (res.code === 200) {
					let data = res.data;
					this.shopItemData = data;
					this.showShopItem = true;
				} else {
					uni.showToast({
						title: res.msg,
						icon:"none"
					})
				}
			});
		},
		goLocation(e) {
			// 回到自己的位置
			let content = uni.createMapContext('map', this);
			content.moveToLocation();
		},
		callMe() {
			// 联系客服
			this.showCallUs = true;
		},
		async scanCode() {
			if (this.orders.length > 0) {
				return uni.showToast({
					title: '存在租借中的订单',
					icon: 'none'
				});
			}
			let data = await scan();
			// 扫码
			if (data.result) {
				// 扫码成功后跳转
				let no = data.result;
				let code = no.split('/');
				let sn = code[code.length - 1];
				if (!sn)
					return uni.showToast({
						title: '二维码格式错误',
						icon: 'none'
					});
				uni.navigateTo({
					url: 'loan?sn=' + sn
				});
			}
		},
		hideShop() {
			this.showShopItem = false;
		},
		regionchange(e) {
			let that = this;
			console.log(e.scale,e)
			if (e.type === 'end' && e.causedBy === 'drag') {
				that.scale = e.scale;
				let content = uni.createMapContext('map', this);
				content.getCenterLocation({
					success(res) {
						that.addressInfo['latitude'] = res['latitude'];
						that.addressInfo['longitude'] = res['longitude'];
						that.getNearDevice({
							lat: that.addressInfo['latitude'],
							lng: that.addressInfo['longitude'],
							range: 2000
						});
						console.log('拖拽后的经纬度：' + res);
					},
					fail(err) {
						uni.showToast({
							title: '获取定位失败',
							icon: 'none'
						});
					}
				});
			}
		},
		goNearShop() {
			let latitude = this.addressInfo.latitude;
			let longitude = this.addressInfo.longitude;
			// 附件门店跳转
			uni.navigateTo({
				url: `nearShop?latitude=${latitude}&longitude=${longitude}`
			});
		},
		goUser() {
			if(this.userInfo){
				// 跳转个人界面
				uni.navigateTo({
					url: 'userInfo'
				});
			} else {
				this.checkAuth();
			}
		},
		goOrderDetails(e) {
			console.log(e)
			// 商户详情
			uni.navigateTo({
				url: `orderDetail?item=${JSON.stringify(e)}`
			});
		},
		getNearDevice(data) {
			// 获取附件的设备
			let that = this;
			post('shop/nearby', data).then(res => {
				console.log(res.msg)
				if (res.code === 200) {
					let data = res.data;
					if (data.length > 0) {
						that.markers = [];
						data.map((item, index) => {
							console.log(item.shopInfo['shopName'])
							that.markers.push({
								id: item.shopInfo['stringId'],
								iconPath: 'http://wd-qidian.oss-cn-beijing.aliyuncs.com/mini/icon-maker.png',
								width: 30,
								height: 30,
								title: item.shopInfo['shopName'],
								latitude: item.shopInfo['latitude'],
								longitude: item.shopInfo['longitude']
							});
						});
					}
				} else {
					uni.showToast({
						title: res.msg,
						icon:"none"
					})
				}
			});
		},
		getUserOrder(data) {
			// 获取用户订单
			post('order/list', data).then(res => {
				if (res.code === 200) {
					this.orders = res.data;
					console.log('請求訂單，',typeof this.outSn,typeof this.orders.length)
					if(this.outSn && this.orders.length < 1){
						uni.navigateTo({
							url: 'loan?sn=' + this.outSn
						});
					}
				}
			});
		}
	}
};
